.btn-order {
    height: 100%;
    background-color: var(--red);
    border-radius: 4px;
    min-height: 28px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color .3s ease-in-out;
    &-text {
        font-size: 1.25rem;
        line-height: 1.5rem;
        color: var(--white);
        font-weight: 500;
        margin-right: 4px;
        transition: color .3s ease-in-out;
    }

    &-svg {
        transition: stroke .3s ease-in-out;
    }

    &:disabled {
        background-color: var(--gray-normal);
    }

    &:disabled &-text {
      color: var(--gray);
    }
    &:disabled &-svg path {
      stroke: var(--gray);
    }
}



