.btn-order {
  height: 100%;
  background-color: var(--red);
  border-radius: 4px;
  min-height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s ease-in-out;
}
.btn-order-text {
  font-size: 1.25rem;
  line-height: 1.5rem;
  color: var(--white);
  font-weight: 500;
  margin-right: 4px;
  transition: color 0.3s ease-in-out;
}
.btn-order-svg {
  transition: stroke 0.3s ease-in-out;
}
.btn-order:disabled {
  background-color: var(--gray-normal);
}
.btn-order:disabled .btn-order-text {
  color: var(--gray);
}
.btn-order:disabled .btn-order-svg path {
  stroke: var(--gray);
}